.home-body {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  text-align: center;
}

.home-body-hori-hr {
  width: 10vw;

  border: solid red;
  border-width: 1.5px;
}

.home-greetingbox-container {
  padding: 0px 5px 0px 5px;
  font-size: 4rem;
  border-radius: 10px;
}

.home-hyperlinkhub {
  display: flex;
  justify-content: center;
}

.home-hyperlinkhub a {
  opacity: 0;
  transition: 0.4s;
}

.home-hyperlinktitle {
  opacity: 0.6;
  transition: 0.4s;
  text-decoration: underline;
  color: white;
}

.onHoverTrigger:hover a {
  opacity: 1;
  color: white;
}
