.skill-category.software {
  height: 38vh;
}

.skill-category.langs {
  height: 13vh;
}

.skill-category.pLangsAndFrameworks {
  height: 25vh;
  margin: 0 2vw;
}

.skills_display-cards {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  display: flex;

  flex-flow: column;
  justify-content: center;
  align-content: center;
}

.skills_display_cards-container {
  width: 300px;
  height: 400px;

  background-color: #141617;

  flex-direction: column;
  display: flex;
  box-shadow: 1px 1px 5px 1px black !important;

  align-items: center;
  border-radius: 10px;
  box-shadow: 3px 3px 15px 2px black !important;
}

.skills_display-cards diva {
  margin-bottom: 4%;

  margin-right: 2px;
}

.skills-body-align {
  display: flex;
  flex-flow: row wrap;
  height: 100%;
  margin-top: 15vh;
  width: 100%;
  justify-content:  center;
  
  
 
}

.spoonfeed-skilllevels {
  margin-left: 45%;
  width: 200px;
  height: 200px;
  }

.spoonfeed-skilllevels div {
  font-size: 1.5rem;
text-align: center;
opacity: 0.4;
transition: 0.3s;
text-decoration: underline;
}

.spoonfeed-skilllevels p{
opacity: 0;
font-size: 0.9rem;
transition: 0.3s;
}

.spoonfeed-skilllevels:hover p {
  opacity: 1;
  font-size: 0.9rem;
  transition: 0.3s;
  }

  .spoonfeed-skilllevels:hover div {
    opacity: 1;
    font-size: 1.5rem;
    transition: 0.3s;
    color: red;
    }






.skills-container-header {
  height: 10vh;
  margin-top: 8vh;
  text-align: center;
  letter-spacing: 4px;
  font-size: 1rem;
  max-lines: 1;
}

.liner-parent-box {
  width: 300px;
  height: 100px;
  margin: 10px;
  background-color: #141617;

  flex-direction: row;
  display: flex;

  align-items: center;
  border-radius: 10px;
  box-shadow: 3px 3px 15px 2px black !important;
}

.liner-metadata-minion {
  color: grey;
  font-style: italic;
  font-size: 0.8rem;
}

.skills-container-offset{
margin: 0 5vw 0 5vw;
}