.projects_display-cards {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  display: flex;

  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
}

.skills_display_cards-container {
  width: 300px;
  height: 400px;
  margin: 2%;
  background-color: #141617;

  flex-direction: column;
  display: flex;
  box-shadow: 1px 1px 5px 1px black !important;
  padding-top: 2vh;

  align-items: center;
  border-radius: 10px;
  box-shadow: 3px 3px 15px 2px black !important;
}

.shadow_light {
  box-shadow: 1px 1px 2px 1px black !important;
}

.projects_display_cards_container-title {
  color: white;
  font-size: 1rem;
}

.projects_display_cards-container-array {
  flex-direction: row;
  display: flex;
}

.projects_display_cards_container-minion {
  height: 100%;
  color: white;
  font-size: 0.9rem;

  display: flex;
  align-items: center;
  text-align: center;
  transition: 0.4s;
  padding: 10px;
}

.projects_display_cards_container-text {
  border-radius: 5px;
  background-color: rgb(0, 136, 255);

  font-size: 0.8rem;
  padding: 1px 5px 1px 5px;
  box-shadow: 1px 1px 5px 1px black !important;
  margin: 10px 10px 20px 10px;
  color: white;
}

.H10_tabulators {
  font-size: 0.9rem;
  transition: 0.4s;
  font-weight: 600;
  color: aliceblue;
}

.H10_tabulators:before {
  content: " \003C";
  color: red;
}

.H10_tabulators:after {
  content: " \002F \003E";
  color: red;
}

.H11_tabulators {
  font-size: 1rem;
  font-weight: bolder;
  transition: 0.4s;
  color: aliceblue;
}

.H11_tabulators:before {
  content: "\007B";
  color: red;
}

.H11_tabulators:after {
  content: "\007D";
  color: red;
}

.projects_display_cards_container-unity {
  border-radius: 5px;

  box-shadow: 1px 1px 4px 1px black !important;
  height: fit-content;

  color: whitesmoke;
  opacity: 0.2;
  transition: 0.4s;

  border-radius: 5px;

  padding: 1px 5px 1px 5px;
  margin: 10px 10px 20px 10px;
}

.projects_display_cards_container-unity:hover {
  opacity: 1;
  transition: 0.4s;
}

.representative {
  height: 15vh;
  margin-top: 8vh;

  text-align: center;
  letter-spacing: 8px;
  font-size: 2rem;
  max-lines: 1;
}

.projects_display_cards_container-date {
  color: rgb(128, 128, 128);
  font-size: 0.6rem;
  font-weight: 400;
  padding-bottom: 5px;
  opacity: 0.9;
}

.projects_display_cards_container-date_worked {
  font-size: 0.6rem;

  font-weight: 400;
}
