.footer {
  height: 15vh;
  background-color: #0b0c0d;
  color: grey;
  font-size: 0.8rem;
}

.footer-splitter_row {
  height: 100%;
  display: flex;
  justify-content: center;
}

.footer-splitter_column {
  flex-flow: column;
  display: flex;
  justify-content: space-between;
}

.footer-splitter_column a,
p {
  text-align: center;
}

.footer-splitter_column a:hover,
p:hover {
  color: red;

  transition: 0.4s;
}
.footer-splitter_row a:hover {
  color: red;

  transition: 0.4s;
}

.H10_footer {
  font-size: 2rem;
  transition: 0.4s;
  font-weight: 600;
  color: white;
}

.H10_footer:before {
  content: " \003C";
  color: red;
}

.H10_footer:after {
  content: " \002F \003E";
  color: red;
}

.H11_footer {
  font-size: 2rem;
  font-weight: bolder;
  transition: 0.4s;
  color: aliceblue;
}

.H11_footer:before {
  content: "\007B";
  color: red;
}

.H11_footer:after {
  content: "\007D";
  color: red;
}

.H10_footer-text_val {
  font-size: 1vw;
  transition: 0.4s;
  font-weight: 200;
}

.H10_footer-text_val:before {
  content: " \003C";
  color: red;
}

.H10_footer-text_val:after {
  content: " \002F \003E";
  color: red;
}
